import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import ProductImages from '../Product/components/ProductImages';
import Checkbox from '../Form/components/Checkbox';

import './styles.css'

export default class Confirm extends PureComponent {
  render () {
    if (!this.props.open) {
      return null
    }

    // console.log(this.props.product);
    // console.log(this.props.options);
    // console.log(this.props);

    const product = this.props.product

    let content
    if (!this.props.success) {
      content = (
        <div className="confirm__content">
          <div className="confirm__product">
            <div className="confirm__title">
              {product.title}
            </div>
            <div className="confirm__media">
              <ProductImages images={product.images} />
            </div>
            <div className="confirm__options">
              <Checkbox
                label="Без печати баннера"
                class="confirm__option"
              />
              <Checkbox
                label="Дизайн баннера"
                class="confirm__option"
              />
            </div>
          </div>

          <div className="confirm__subtitle">
            Закажите аксессуары к этому товару
          </div>

          <div className="confirm__accessories">
            {product.title}<br/>
            {product.title}
          </div>

          <div className="confirm__total">
            457 р.
          </div>

          <button
            className="confirm__order"
            onClick={this.props.buy}>
            Купить
          </button>
        </div>
      )
    } else {
      content = (
        <div className="confirm__success">
          <div className="confirm__message">
            Товар добавлен в корзину!
          </div>
          <div className="confrim__actions">
            <button
              className="confirm__action"
              onClick={this.props.close}>
              Оформить заказ
            </button>
            <button
              className="confirm__action"
              onClick={this.props.close}>
              Продолжить покупки
            </button>
          </div>
        </div>
      )
    }


    return (
      <div>
      <div className="modal">
        {content}

        <div
          className="modal__close"
          onClick={this.props.close}>
          ×
        </div>
      </div>
      <div className="fade" onClick={this.props.close}></div>
      </div>
    )
  }
}

Confirm.propTypes = {
  open: PropTypes.bool.isRequired,
  product: PropTypes.object.isRequired,
  options: PropTypes.object,
  close: PropTypes.func
}



