import React, { Component } from 'react'
import PropTypes from 'prop-types'

import Locations from '../Locations'
import Hamburger from '../Hamburger'
import Branding from '../Branding'
import Search from '../Search'
import Shipping from '../Shipping'
import Order from '../Order'
import CatalogBrowser from '../CatalogBrowser'
import Textfield from '../Form/components/Textfield'

import './styles.css'

export default class Header extends Component {
  constructor (props) {
    super(props)

    this.state = {
      openDrawer: false,
      openBrowser: false,
      openTopDrawer: false,
      openSelectCity: false,
      city: "",
      cities: [ "Алматы", "Архангельск", "Астана", "Владивосток", "Волгоград", "Воронеж", "Гродно", "Екатеринбург", "Казань", "Калининград", "Краснодар", "Красноярск", "Минск", "Москва", "Нижний Новгород", "Новосибирск", "Омск", "Пермь", "Ростов-на-Дону", "Самара", "Санкт-Петербург", "Севастополь", "Симферополь", "Сочи", "Уфа", "Челябинск", "Шымкент", "Ялта" ],
      period: "",
      orderTotal: "",
      shippingPrice: ""
    }

    this.toggleDrawer = this.toggleDrawer.bind(this)
    this.toggleBrowser = this.toggleBrowser.bind(this)
    this.toggleTopDrawer = this.toggleTopDrawer.bind(this)
    this.toggleSelectCity = this.toggleSelectCity.bind(this)
    this.selectCity = this.selectCity.bind(this)
    this.fillCart = this.fillCart.bind(this)
  }

  toggleDrawer () {
    this.setState({openDrawer: !this.state.openDrawer})
  }

  toggleBrowser () {
    this.setState({openBrowser: !this.state.openBrowser})
  }

  toggleTopDrawer () {
    this.setState({openTopDrawer: !this.state.openTopDrawer})
  }

  toggleSelectCity () {
    this.setState({openSelectCity: !this.state.openSelectCity})
  }

  selectCity (e) {
    // console.log(e.target.innerHTML);
    this.setState({
      city: e.target.innerHTML,
      period: "10-15 дней",
      shippingPrice: "734 р."
    })
    this.toggleSelectCity()
  }

  fillCart () {
    if (!this.state.orderTotal) {
      this.setState({
        orderTotal: "18 000 р."
      })
    } else {
      this.setState({
        orderTotal: ""
      })
    }
  }

  render () {
    const panelClasses = [ 'panel' ]
    if (this.state.openDrawer) { panelClasses.push('opened') }
    const panelContentClasses = [ 'panel__content' ]
    if (this.state.openDrawer) { panelContentClasses.push('opened') }

    const topDrawerClasses = [ 'top_drawer' ]
    if (this.state.openTopDrawer) { topDrawerClasses.push('opened') }

    return (
      <div>
      <div
        className={topDrawerClasses.join(' ')}
        onClick={this.toggleTopDrawer}>
        <img src="http://springhead.su/springhead.png" alt=""/>
      </div>
      <header className="header">
        <div className="header__top">
          <ul
            className="secondary_menu"
            onClick={this.toggleTopDrawer}>
            <li>Доставка</li>
            <li>Оплата</li>
            <li>Контакты</li>
            <li>О компании</li>
          </ul>
        </div>
        <div className="header__menu_toggle">
          <Hamburger
            onOpenDrawer={this.toggleDrawer} />
        </div>
        <div className="header__branding">
          <Branding
            class="branding--default" />
        </div>
        <div className="header__locations">
          <Locations
            city={this.state.city}
            onSelectCity={this.toggleSelectCity}
          />
        </div>
        <div className="header__search">
          <Search
            onOpenBrowser={this.toggleBrowser} />
        </div>

        {/* Drawer */}
        <div className={panelClasses.join(' ')} onClick={this.toggleDrawer}></div>
        <div className={panelContentClasses.join(' ')}>
          <div
            className="panel__close"
            onClick={this.toggleDrawer}>
            <i className="fa fa-arrow-left" aria-hidden="true"></i>
          </div>

          <Branding
            class="branding--compact"/>

          <div className="panel__block">
            <Order
              orderTotal={this.state.orderTotal}
              shippingPrice={this.state.shippingPrice}
              onFillCart={this.fillCart}
            />
          </div>

          <div className="panel__block">
            <Shipping
              city={this.state.city}
              period={this.state.period}
              onSelectCity={this.selectCity} />
          </div>

          <div className="panel__block">
            <div className="panel_menu">
              <div className="panel_menu__header">
                Контакты
              </div>
              <div className="panel_menu__subheader">
                Адреса офисов, телефоны, реквизиты
              </div>
              <div className="panel_menu__arrow">
                <i className="fa fa-angle-right fa-2x" aria-hidden="true"></i>
              </div>
            </div>
          </div>
        </div>

        {this.state.openBrowser &&
          <div>
            <div
              className="fade"
              onClick={this.toggleBrowser}></div>
            <div className="browser modal">
              <div
                className="modal__close"
                onClick={this.toggleBrowser}>
                ×
              </div>
              <Branding
                class="branding--search"/>

              <div className="browser__search fa-search">
                <Textfield
                  type="text"
                  name="search"
                  placeholder="Название или артикул товара"
                />
              </div>

              <div className="browser__catalog">
                <CatalogBrowser />
              </div>
            </div>
          </div>
        }

        {this.state.openSelectCity &&
          <div>
            <div
              className="fade"
              onClick={this.toggleSelectCity}></div>
            <div className="select_city modal">
              <div
                className="modal__close"
                onClick={this.toggleSelectCity}>
                ×
              </div>
              <Branding
                class="branding--search"/>

              <div className="select_city__list">
                <ul>
                  {this.state.cities.map((city, index) =>
                    <li
                      key={index}
                      onClick={this.selectCity}>
                      {city}
                    </li>
                  )}
                </ul>
              </div>
            </div>
          </div>
        }
      </header>
      </div>
    )
  }
}

Header.propTypes = {}



