import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Filter from './components/Filter';
import './styles.css'

export default class Filters extends PureComponent {
  render () {
    const category = this.props.category
    const settings = this.props.settings

    let filters
    if (settings.type) {
      filters = category.types[settings.type].filters
    } else {
      filters = category.filters
    }

    const filterElements = []

    if (category.types) {
      const widget = "type"
      const name = "type"
      filterElements.push(
        <Filter
          key="type"
          name={name}
          value={settings.type}
          onApplyFilter={this.props.onApplyFilter.bind(null, {widget, name})}
          {...category.types}
        />
      )
    }

    Object.keys(filters).map(index => {
      if (filters[index].widget == "links") {
        const value = settings.filters[index]
        const name = index
        const widget = "link"
        filters[index].options.map((option, index) => {
          filterElements.push(
            <Filter
              key={name + '_' + index}
              widget={widget}
              title={option}
              url=""
              value={ value && value.indexOf(option) !== -1 }
              onApplyFilter={this.props.onApplyFilter.bind(null, {widget, name, option})}
            />
          )
        })
      } else {
        const widget = filters[index].widget
        const name = index
        filterElements.push(
          <Filter
            key={index}
            name={index}
            value={settings.filters[index]}
            onApplyFilter={this.props.onApplyFilter.bind(null, {widget, name})}
            {...filters[index]}
          />
        )
      }
    })

    return (
      <div className="filters">
        {filterElements}
      </div>
    )
  }
}

Filters.propTypes = {
  filters: PropTypes.object,
  onApplyFilter: PropTypes.func
}



