import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'

import Catalog from '../../components/Catalog'
import Select from '../Form/components/Select';

import './styles.css'

import dummyActivityJust from '../../data/dummy_activityJust.json'
import dummyActivityPrint from '../../data/dummy_activityPrint.json'

export default class Activity extends Component {
  constructor (props) {
    super(props)

    this.state = {
      title: "",
      categories: [],
      activeCategory: ""
    }

    this.selectCategory = this.selectCategory.bind(this)
  }

  componentDidMount () {
    const activities = {
      just: dummyActivityJust,
      print: dummyActivityPrint
    }

    const activity = activities[this.props.name]

    this.setState({
      title: activity.title,
      categories: activity.categories,
      activeCategory: activity.activeCategory
    })
  }

  selectCategory (e) {
    // console.log(e.target.value);
    this.setState({
      activeCategory: e.target.value
    })
  }

  render () {
    return (
      <div className="activity">
        <h2 className="activity__header">
          {this.state.title}
        </h2>

        <div className="activity__categories">
          {/*}
          <Select
            selected={this.state.activeCategory}
            options={this.state.categories}
            controlFunc={this.selectCategory}
            class="filter__select"
          />
          */}
          <Link to="/catalog/stands">Мобильные стенды</Link>
        </div>

        {this.state.activeCategory &&
          <Catalog
            category={this.state.activeCategory}
            mode={this.props.mode}
          />
        }
      </div>
    )
  }
}

Activity.propTypes = {
  name: PropTypes.string.isRequired,
  mode: PropTypes.string.isRequired
}



