import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

/**
 * Опции товара
 */
export default class ProductOptions extends PureComponent {
  render () {
    const options = this.props.options
    const activeOptions = []
    Object.keys(options).map(index => {
      if ((options[index].mode === "on" && options[index].value) ||
          (options[index].mode === "off" && !options[index].value)) {
            activeOptions.push(
              <li key={index}>
                <i className="fa fa-check" aria-hidden="true"></i>    {options[index].title}
              </li>
            )
          }
    })

    return (
      <ul>
        {activeOptions}
      </ul>
    )
  }
}

ProductOptions.propTypes = {
  options: PropTypes.object
}



