import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import './styles.css'

export default class Branding extends PureComponent {
  render () {
    const classes = [ 'branding' ]
    if (this.props.class) { classes.push(this.props.class) }

    return (
      <div className={classes.join(' ')}>
        <Link to="/" className="branding__logo">
          <img src="/images/logo.png" alt="" />
        </Link>
        <div className="branding__slogan">
          <span>Сервис по подбору рекламной продукции</span>
        </div>
      </div>
    )
  }
}

Branding.propTypes = {}



