import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import './styles.css'

export default class Order extends PureComponent {
  render () {
    return (
      <div
        className="order"
        onClick={this.props.onFillCart}>
        <div className="order__cart">
          <i className="fa fa-shopping-cart" aria-hidden="true"></i>
        </div>
        <div className="order__order">
          {this.props.orderTotal ?
            "Сумма заказа: " + this.props.orderTotal :
            "В корзине еще нет товаров"
          }
        </div>
        {this.props.shippingPrice &&
          <div className="order__shipping">
            Минимальная стоимость доставки: {this.props.shippingPrice}
          </div>
        }
        {/*
          <div className="order__arrow">
          <i className="fa fa-angle-right fa-2x" aria-hidden="true"></i>
        </div>
        */}
      </div>
    )
  }
}

Order.propTypes = {
  orderTotal: PropTypes.string,
  shippingPrice: PropTypes.string
}



