import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

/**
 * Характеристики товара
 */
export default class ProductSpecs extends PureComponent {
  render () {
    const specs = this.props.specs.map((spec) => {
      let value, name

      if (spec.value.constructor === Array) {
        value = spec.value.join(', ')
      } else {
        value = spec.value
      }

      if (spec.name) {
        name = spec.name + ': '
      } else {
        name = ''
      }

      return {name: name, value: value}
    })

    return (
      <ul className="product__specs">
        {specs.map((spec, index) =>
          <li key={index}>
            {spec.name && <span>{spec.name}</span>}{spec.value}</li>
        )}
      </ul>
    )
  }
}

ProductSpecs.propTypes = {
  specs: PropTypes.array
}



