import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Select from '../../Form/components/Select';
import Checkbox from '../../Form/components/Checkbox';
import Textfield from '../../Form/components/Textfield';
import Button from '../../Form/components/Button';

/**
 * Одиночный фильтр
 * Может быть следующих типов:
 *  - выпадающий список
 *  - текстовое поле
 *  - ссылка (кнопка)
 */
export default class Filter extends PureComponent {
  render () {
    switch (this.props.widget) {
      case 'select':
        return (
          <div className="filter filter--select">
            <Select
              label={this.props.label}
              selected={this.props.value}
              options={this.props.options}
              controlFunc={this.props.onApplyFilter}
              class="filter__select"
            />
          </div>
        )
        break;

      case 'checkbox':
        return (
          <div className="filter filter--checkbox">
            <Checkbox
              label={this.props.label}
              value={this.props.value}
              controlFunc={this.props.onApplyOption}
              class="filter__checkbox"
            />
          </div>
        )
        break;

      case 'textfield':
        return (
          <div className="filter">
            <Textfield
    					inputType={'text'}
    					name={this.props.name}
    					controlFunc={this.props.onApplyFilter}
    					content={this.props.value}
    					placeholder={this.props.name}
              type="text"
            />
          </div>
        )
        break;

      case 'link':
        return (
          <div className="filter filter--link">
            <Button
              value={this.props.value}
              title={this.props.title}
              controlFunc={this.props.onApplyFilter}
              class="filter__button"
            />
          </div>
        )
        break;

      default:
        return null
    }
  }
}



