import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Filter from '../Filters/components/Filter';
import './styles.css'

export default class Options extends PureComponent {
  render () {
    const category = this.props.category
    const settings = this.props.settings

    let options
    if (settings.type) {
      options = category.types[settings.type].options
    } else {
      options = category.options
    }

    const optionElements = []
    Object.keys(options).map(index => {
      const name = index
      const mode = options[index].mode
      const label = options[index].label
      optionElements.push(
        <Filter
          key={index}
          value={settings.options[index]}
          onApplyOption={this.props.onApplyOption.bind(null, {name, mode})}
          {...options[index]}
        />
      )
    })

    return (
      <div className="options">
        {optionElements}
      </div>
    )
  }
}

Options.propTypes = {
  options: PropTypes.object,
  onApplyOption: PropTypes.func
}



