import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Filter from '../Filters/components/Filter';
import './styles.css'

export default class Mode extends PureComponent {
  render () {
    return (
      <div className="mode">
        <Filter
          onApplyOption={this.props.onApplyOption}
          {...this.props.mode}
        />
      </div>
    )
  }
}

Mode.propTypes = {
  mode: PropTypes.object,
  onApplyOption: PropTypes.func
}



