import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Product from '../Product';
import './styles.css'

export default class ProductList extends Component {
  constructor (props) {
    super(props)

    this.state = {
      single: false,
      activeProductId: ''
    }

    this.toggleMode = this.toggleMode.bind(this)
    this.order = this.order.bind(this)
  }

  order (id) {
    this.toggleMode({id: this.state.activeProductId})
    this.props.onOrder(id)
  }

  toggleMode(obj, e, value) {
    // console.log(obj, e, value);
    let single
    let activeProductId
    if (obj.id == this.state.activeProductId) {
      single = false
      activeProductId = ''
    } else {
      single = true
      activeProductId = obj.id
    }

    this.setState({ single, activeProductId })
  }

  render () {
    const category = this.props.category
    const settings = this.props.settings
    const products = this.props.products

    let filters
    if (settings.type) {
      filters = category.types[settings.type].filters
    } else {
      filters = category.filters
    }

    // console.log(filters);
    // console.log(settings);
    // console.log(products);

    // Build conditions
    let conditions = []
    if (settings.type) {
      conditions.push(product =>
        product.type.includes(settings.type)
      )
    }
    if (settings.filters.size) {
      conditions.push(product =>
        !settings.filters.size ||
        product.specs[0].value.includes(settings.filters.size)
      )
    }
    if (settings.filters.features) {
      conditions.push(product =>
        !settings.filters.features.length ||
        product.features.filter(feauture =>
          settings.filters.features.includes(feauture)
        ).length == settings.filters.features.length
      )
    }
    if (settings.filters.search) {
      conditions.push(product =>
        product.title.toLowerCase().includes(settings.filters.search.toLowerCase())
      )
    }

    // Filter products
    let filteredProducts = products.filter(product =>
      conditions.every(condition =>
        condition(product)
      )
    )

    // Mode imitation
    if (settings.mode == 'short') {
      filteredProducts = filteredProducts.slice(0, 3)
    }

    const classes = [ 'product_list' ]
    if (this.state.single) { classes.push('product_list--single') }

    return (
      <div className={classes.join(' ')}>
        {filteredProducts.map((product, index) => {
          const classes = [ 'product_list__item' ]
          if (product.id == this.state.activeProductId) { classes.push('active') }
          return (
            <div key={index} className={classes.join(' ')}>
              <Product
                options={this.props.category.options}
                active={product.id == this.state.activeProductId}
                onToggleMode={this.toggleMode}
                onOrder={this.order}
                {...product}
              />
            </div>
          )}
        )}

        {settings.mode == 'short' &&
          <div className="product_list__placeholder">
            <span>
              Мне это не подходит :(<br/>перейду-ка я в каталог
            </span>
          </div>
        }
      </div>
    )
  }
}

ProductList.propTypes = {
  products: PropTypes.array,
  category: PropTypes.object,
  mode: PropTypes.string
}



