import React, { Component } from 'react'
import PropTypes from 'prop-types'

import ProductList from '../ProductList';
import Filters from '../Filters';
import Options from '../Options';
import Mode from '../Mode';
import Confirm from '../Confirm';

import './styles.css'

import dummy_categoryStands from '../../data/dummy_categoryStands.json'
import dummy_productsStands from '../../data/dummy_productsStands.json'

export default class Catalog extends Component {
  constructor (props) {
    super(props)

    this.state = {
      category: {},
      settings: {
        filters: {},
        options: {},
        mode: '',
        type: ''
      },
      products: [],
      confirm: {
        open: false,
        product: {},
        success: false
      }
    }

    this.applyFilter = this.applyFilter.bind(this)
    this.applyOption = this.applyOption.bind(this)
    this.applyMode = this.applyMode.bind(this)
    this.toggleConfirm = this.toggleConfirm.bind(this)
    this.buyProduct = this.buyProduct.bind(this)
  }

  componentDidMount () {
    this.init(this.props)
  }

  componentWillReceiveProps(nextProps) {
    this.init(nextProps)
  }

  toggleConfirm (id) {
    let confirm
    if (this.state.confirm.open) {
      confirm = Object.assign({}, this.state.confirm, {
        open: false,
        product: {},
        success: false
      })
    } else {
      const products = this.state.products
      const product = products.find(product =>
        product.id == id
      )
      confirm = Object.assign({}, this.state.confirm, {
        open: true,
        product: product,
        success: false
      })
    }

    this.setState({
      confirm: confirm
    })
  }

  buyProduct () {
    const confirm = Object.assign({}, this.state.confirm, {
      success: true
    })

    this.setState({
      confirm: confirm
    })
  }

  getFilters () {
    fetch('/data/dummy_filtersValues.json')
      .then(res => res.json())
      .then(data => {
        this.setFilters(data)
      })
  }

  getCategory () {
    fetch('/data/dummy_categoryStands.json')
      .then(res => {
        return res.json()
      })
      .then(data => {
        this.setState(data)
        return fetch('/data/dummy_productsStands.json')
      })
      .then(res => {
        return res.json()
      })
      .then(data => {
        this.setState(products => data)
        return fetch('/data/dummy_filtersValues.json')
      })
      .then(res => {
        return res.json()
      })
      .then(data => {
        this.setFilters(data)
      })
  }

  init (props) {
    const categories = {
      "Мобильные стенды": {
        category: dummy_categoryStands,
        products: dummy_productsStands
      }
    }
    const data = categories[props.category]

    if (!data) {
      this.setState({
        category: {},
        settings: {
          filters: {},
          options: {},
          mode: '',
          type: ''
        },
        products: []
      })

      return
    }

    const type = data.category.types ? data.category.types.options[0] : ""
    const mode = props.mode ? props.mode : ""

    // let options
    // if (type) {
    //   options = data.category.types[type].options
    // } else {
    //   options = data.category.options
    // }
    // let defaultOptions = {}
    // Object.keys(options).map(key => {
    //   if (options[key].mode == "off") {
    //     defaultOptions[key] = true
    //   }
    // })

    const defaultSettings = Object.assign({}, this.state.settings, {
      type: type,
      mode: mode
    })

    this.setState({
      category: data.category,
      products: data.products,
      settings: defaultSettings
    })
  }

  initCategory () {
    Promise.all([
      fetch('/data/dummy_categoryStands.json'),
      fetch('/data/dummy_productsStands.json')
    ])
    .then(results => {
      let data = {}
      results.map(res =>
        res.json()
          .then(json => {
            Object.assign(data, json)
            console.log(1);
          })
      )
      this.setState(data)
      console.log(2);
    })
    .then(() => {
      this.getFilters();
      console.log(3);
    })
  }

  setFilters (data) {
    const category = this.state.category
    const settings = this.state.settings

    let filters
    if (settings.type) {
      filters = category.types[settings.type].filters
    } else {
      filters = category.filters
    }

    let newFilters = {}
    Object.keys(filters).map(key => {
      if (data.filters && data.filters.hasOwnProperty(key)) {
        switch (filters[key].widget) {
          case 'links':
            let newValue = []
            if (settings.filters[key] && settings.filters[key].indexOf(data.filters[key]) !== -1) {
              newValue = settings.filters[key].filter(s => s !== data.filters[key])
            } else {
              newValue = settings.filters[key] ? settings.filters[key].concat([data.filters[key]]) : [data.filters[key]]
            }
            newFilters[key] = newValue
            break;

          default:
            newFilters[key] = data.filters[key]
        }
      } else {
        newFilters[key] = settings.filters[key]
      }
    })

    let options
    if (settings.type) {
      options = category.types[settings.type].options
    } else {
      options = category.options
    }
    let newOptions = {}
    Object.keys(options).map(key => {
      if (data.options && data.options.hasOwnProperty(key)) {
        newOptions[key] = data.options[key]
      } else {
        newOptions[key] = settings.options[key]
      }
    })

    const mode = settings.mode
    let newMode = {}
    if (data.hasOwnProperty("mode")) {
      newMode = data["mode"]
    } else {
      newMode = mode
    }

    const type = settings.type
    let newType = {}
    if (data.hasOwnProperty("type")) {
      newType = data["type"]
    } else {
      newType = type
    }

    const newSettings = {
      filters: newFilters,
      options: newOptions,
      mode: newMode,
      type: newType
    }

    this.setState({
      settings: newSettings
    })
  }

  applyFilter (obj, e) {
    // console.log(obj, e);
    let data = {}
    switch (obj.widget) {
      case 'type':
        data = {
          type: e.target.value
        }
        break
      case 'link':
        data = {
          filters: { [obj.name]: obj.option}
        }
        break
      case 'select':
        data = {
          filters: { [obj.name]: e.target.value }
        }
        break
      case 'textfield':
        data = {
          filters: { [obj.name]: e.target.value }
        }
        break
    }

    this.setFilters(data)
  }

  applyOption (obj, e) {
    // console.log('obj: ', obj, 'value: ', e.target.checked);
    const data = {
      options: {
        [obj.name]: e.target.checked
      }
    }

    this.setFilters(data)
  }

  applyMode (e, isInputChecked) {
    // console.log(e, isInputChecked);
    const data = {
      "mode": e.target.checked
    }

    this.setFilters(data)
  }

  render () {
    return (
      <div className="catalog">
        {this.state.category.filters &&
          <Filters
            category={this.state.category}
            settings={this.state.settings}
            onApplyFilter={this.applyFilter}
          />
        }
        {this.state.category.options &&
          <Options
            onApplyOption={this.applyOption}
            category={this.state.category}
            settings={this.state.settings}
          />
        }
        {/*
        {this.state.options &&
          <Mode
            onApplyOption={this.applyMode}
            mode={this.state.mode}
          />
        }
        */}
        {this.state.category.filters &&
          <ProductList
            category={this.state.category}
            settings={this.state.settings}
            products={this.state.products}
            onOrder={this.toggleConfirm}
          />
        }
        <Confirm
          open={this.state.confirm.open}
          product={this.state.confirm.product}
          options={this.state.options}
          close={this.toggleConfirm}
          buy={this.buyProduct}
          success={this.state.confirm.success}
        />
      </div>
    )
  }
}

Catalog.propTypes = {
  category: PropTypes.string.isRequired,
  mode: PropTypes.string.isRequired,
  type: PropTypes.string
}



