import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import './styles.css'

export default class Locations extends PureComponent {
  render () {
    return (
      <div className="locations">
        <div className="locations__phone">8 800 505 17 30</div>
        <div className="locations__shipping">
          <div className="locations__all">Доставка по России и СНГ</div>
          <div
            className="locations__city"
            onClick={this.props.onSelectCity}>
            <span>Ваш город:</span> <span className="locations__current">
              {this.props.city ? this.props.city : "Выбрать город"}
            </span>
          </div>
        </div>
      </div>
    )
  }
}

Locations.propTypes = {
  city: PropTypes.string
}



