import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import ProductImages from './components/ProductImages';
import ProductOptions from './components/ProductOptions';
import ProductSpecs from './components/ProductSpecs';
import './styles.css'

export default class Product extends PureComponent {
  render () {
    // console.log(this.props);
    const active = this.props.active
    const classes = [ 'product' ]
    if (active) { classes.push('product--single') }

    return (
      <article className={classes.join(' ')}>
        <div
          className="product__media"
          onClick={this.props.onToggleMode.bind(null, {id: this.props.id})}>
          <ProductImages images={this.props.images}/>
        </div>

        <div className="product__offer">
          <div className="product__price">
            {this.props.price.cost} р.
          </div>
          <div className="product__options">
            <ProductOptions
              options={this.props.options}/>
          </div>
        </div>

        <div className="product__header">
          <h2 className="product__title">
            {this.props.title}
          </h2>
          <div className="product__advantages">
            {this.props.advantages}
          </div>
        </div>

        <div className="product__content">
          <ProductSpecs specs={this.props.specs}/>
        </div>

        <div className="product__order">
          <button
            className="product__submit"
            onClick={this.props.onOrder.bind(null, this.props.id)}>
            Заказать
          </button>
        </div>
      </article>
    )
  }
}

Product.propTypes = {
  title: PropTypes.string,
  advantages: PropTypes.string,
  images: PropTypes.array,
  price: PropTypes.object,
  options: PropTypes.object,
  specs: PropTypes.array,
  options: PropTypes.object
}



