import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import './styles.css'

export default class Shipping extends PureComponent {
  render () {
    return (
      <div
        className="shipping"
        onClick={this.props.onSelectCity}>
        <div className="shipping__target">
          <i className="fa fa-dot-circle-o" aria-hidden="true"></i>
        </div>
        <div className="shipping__city">
          {this.props.city ? this.props.city : "Доставка по России"}
        </div>
        <div className="shipping__period">
          {this.props.period ?
            "Срок доставки: " + this.props.period :
            "Выберите город, чтобы узнать срок и стоимость доставки"
          }
        </div>
        <div className="shipping__select">
          <span>Изменить город доставки</span>
        </div>
        {/*
        <div className="shipping__arrow">
          <i className="fa fa-angle-down fa-2x" aria-hidden="true"></i>
        </div>
        */}
      </div>
    )
  }
}

Shipping.propTypes = {
  city: PropTypes.string,
  period: PropTypes.string
}



