import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'

import Header from '../../components/Header'
import Activity from '../../components/Activity'

import './styles.css'

export default class Home extends PureComponent {
  render () {
    return (
      <div className="page">
        <Header />

        <main>
          <div className="breadcrumbs">
            <ul>
              <li><Link to="/">Главная</Link></li>
              <li>Выставочные системы</li>
              <li><Link to="/catalog/stands">Мобильные стенды</Link></li>
            </ul>
          </div>
          <section className="region region--odd">
            <Activity
              name="just"
              mode="short"
            />
          </section>

          <section className="region region--even">
            <Activity
              name="print"
              mode="short"
            />
          </section>
        </main>
      </div>
    )
  }
}



