import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import './styles.css'

export default class Hamburger extends PureComponent {
  render () {
    return (
      <a href="#"
        className="hamburger"
        onClick={this.props.onOpenDrawer}>
        <i className="fa fa-bars fa-2x" aria-hidden="true"></i>
      </a>
    )
  }
}

Hamburger.propTypes = {}



